module.exports = {"main":{"name":"Dimitris Selalmazidis","occupation":"Full Stack Software Engineer","employer":" American Express","jobDescription":"Full Stack Engineer","image":"profilepic.jpeg","bio":"Highly motivated and enthusiastic software engineer with strong academic background and commercial experience leveraging technical knowledge, project management skills and passion for technology. Skilled communicator able to navigate fast-paced team environments, engage clients, and motivate team success. Analytical and organized as well as highly adaptable, proactive, and innovative in approach to designing solutions to complex problems.","contactMessage":"Have a project for me? Think I'd be a good fit for your team? I'd love to hear from you, give me a shout by email or by using the form below if you'd like to get in contact with me.","email":"d.selal@hotmail.com","phone":"+44 (0)785-868-8532","address":{"street":"Robert Street","city":"Brighton","state":"East Sussex","zip":"BN1 4AY"},"website":"http://www.dimitris-selalmazidis.com","resumedownload":"https://drive.google.com/uc?id=1hlMroLqSbSCHt4Vy-ZPEDCxsaYi8gwLl&export=download","social":[{"name":"github","url":"https://github.com/theMitsosS","className":"fa fa-github"},{"name":"linkedin","url":"https://www.linkedin.com/in/dimitris-selalmazidis/","className":"fa fa-linkedin"},{"name":"stack-overflow","url":"https://stackoverflow.com/users/8908151/dimitris-selalmazidis?tab=profile","className":"fa fa-stack-overflow"},{"name":"facebook","url":"https://www.facebook.com/jim1924","className":"fa fa-facebook"},{"name":"instagram","url":"https://www.instagram.com/themitsoss","className":"fa fa-instagram"},{"name":"twitter","url":"https://twitter.com/theMitsosS","className":"fa fa-twitter"}]},"resume":{"skillmessage":"I'm a tinkerer and am always interested in playing around with new technologies but these are the ones that have really won me over","education":[{"school":"University of Victoria","degree":"Bachelors in Computer Science, Software Engineering Option","graduated":"Expected April 2018","description":"Under the recommendation and Guidance of the Head Coach of the UVIC soccer team, I transferred to the University of Victoria. Here I took my Bachelors of Computer Science with a Software Engineering Specialty and achieved a much more thorough education in the underlying theory, concepts, and science behind more complex programming. It was here that I discovered my passion for scalable application programming (web and mobile), the security behind computer and application systems, and found that I have a knack for understanding complex algorithms."},{"school":"Grande Prairie Regional College","degree":"Computer Systems Technology","graduated":"April 2013","description":"I attended GPRC for my first 2 years of post-secondary on a scholarship for the college soccer team. While here, I studied Computer Systems Technology, a 2 year lead-in to a Bachelors of Computer Science. This was a very hands-on experience focused more on building projects than general theory. Here I built database structures for fictional companies, created small video games, hand-coded Space Invaders from scratch in Assembly Language, built and coded an Arduino Robot to navigate obstacle courses, a lot of Java and C++ programming for data structures, and much more."}],"work":[{"company":"Shop Your Own Mortgage","title":"Software Security Engineer","years":"August 2017 - Present","description":"For most of my time at Shop Your Own Mortgage, I have worked as a Full Stack Software Engineer. Here I have had the chance to dig in and get my hands dirty with all parts of our software. I have worked with our legacy PHP backend, as well as built new node-based servers for new products. As well, I led the development of V2 of our mobile application written in React Native. I have worked extensively with React/Redux as well as with react hooks. \n\nMore recently, I have been given the opportunity to lead our cybersecurity initiative. As part of our partnership with a number of 3rd parties and our planned expansion into Europe, the need to tighten security and bring our technology up to certain compliance standards has become evident. It is my job to bring us into alignment with these, as well as hardening our security as a whole."},{"company":"City of Fort St. John","title":"Utility Maintenance Technician 1 / Labourer ","years":"April 2014 - September 2017","description":"Working for the City of Fort St. John has given me a solid understanding of a city's infrastructure system and what it takes to keep the things working that we take for granted every day. During my time here I have worked in all roles involved in the Water and Sewer Department, with the exception of equipment operating. I worked as a water meter technician programming the meters in people's homes. I worked in location, excavation, installation and repair of water and sewer lines. I learned how to install, dismantle, troubleshoot, and repair the most popular models of fire hydrants. \n\nWhile working for the Water Treatment and Distribution Department, and alongside one partner, we led the first Uni-Directional Flow program our city has done in years. This requires a thorough understanding of the city's water grid to selectively force excessive pressure through a water line in order to clean it. This must be done in a specific order for every line in the city.   "},{"company":"School District 60","title":"IT Technician","years":"April 2012 - September 2013","description":"Here I was in charge of various duties involved in keeping the school district's computer systems running smoothly. I performed a varied range of tasks including, but not limited to, reimaging the laptops that the students were given, setting up computer labs, setting up and troubleshooting servers, secure discarding of sensitive data, and a small amount of network analysis."}],"skills":[{"name":"Git","description":"I use Git for all of my projects so far. I have used Git for version control in small teams and have toyed around with contributing to Open Source Projects.","image":"git.jpg"},{"name":"Java","description":"Java was my first programming language that I started using in college years ago. It is by far the language I am most familiar with in terms of data-structure and algorithm programming.","image":"java.jpg"},{"name":"React Js","description":"I am fairly new to React and really did not like it at first. It has grown on me though and I will continue to use it going forward. I have built a ReactJS based resume-website template (link below in portfolio) which I used as a starting point for this personal site.","image":"react.jpg"},{"name":"Meteor JS","description":"I enjoy MeteorJS for it's ease of configuration and its ability to get a full stack prototype up and running quickly. I used MeteorJS in a team for a Startup Programming class in UVIC in conjunction with React to build the MVP for Smirkspace (below in portfolio)","image":"meteor.jpg"},{"name":"Node JS","description":"Node JS is my server-side scripting language of choice. The bulk of my experience with it has been in conjunction with MeteorJS-based projects","image":"node.jpg"},{"name":"Bootstrap 4","description":"I use Bootstrap more often than not when designing the layout for my sites. It is the framework I am most familiar with and can therefore design more quickly than with others.","image":"bootstrap.jpg"},{"name":"MongoDB","description":"I have used many different database systems and am better educated and experienced with Relational Databases. However, for many applications, the ease of use of MongoDB better suits my needs and it is much more enjoyable to work with","image":"mongo.jpg"},{"name":"Heroku","description":"I use Heroku as my hosting platform for most of my projects because so far they are all unpaid and their free tier is convenient. I also appreciate its integration with Github for such simple deployment.","image":"heroku.jpg"}]},"portfolio":{"projects":[{"title":"Canadian Wanderlust","category":"My Travel Blog for my post-university travels","image":"canadian-wanderlust.jpg","url":"https://www.canadianwanderlust.com"},{"title":"Fury Fighting Gear","category":"(offline now) A fighting gear company I started","image":"fury-fighting-gear.jpg","url":"http://www.dimitris-selalmazidis.com"},{"title":"Original Thai Food","category":"Website I built for a restaurant I like in Thailand","image":"original-thai-food.jpg","url":"https://original-thai-food.herokuapp.com/index.html"},{"title":"Resume Website","category":"A React based resume website template","image":"resume-website.jpg","url":"https://github.com/tbakerx/react-resume-template"},{"title":"Smirkspace","category":"(MVP Only) A React and Meteor based chat University project.","image":"smirkspace.jpg","url":"http://www.smirkspace.com"}]},"testimonials":{"testimonials":[{"text":"I have had the opportunity to work with Tim on multiple occasions which has always been a pleasure. He is driven and motivated with a consistently positive attitude. Tim excels at communicating with others and is always willing to work hard until he solves the problem at hand.","user":"Alastair Beaumont - Web Developer at AppColony"}]}}

